import React, { useContext } from "react";
import { useState } from "react";
import { Link, NavLink } from "react-router-dom";
import { AppContext } from "../context/AppContext";

const Header = () => {
    //#region Theme
    const changeTheme = (theme: 'dark' | '') => {
        let html = document.querySelector('html');
        if (theme === 'dark' && !html?.classList.contains('dark')) {
            updateButtonValue('☀');
            html?.classList.add(theme);
        } else {
            html?.classList.remove('dark');
            updateButtonValue('🌑')
        }
    }

    const isDarkTheme = (): boolean => {
        let html = document.querySelector('html');
        if (html?.classList.contains('dark')) {
            return true;
        } else {
            return false;
        }
    }
    const [buttonValue, updateButtonValue] = useState(isDarkTheme() ? '☀' : '🌑');

    //#endregion

    //#region AppState

    let appState = useContext(AppContext)

    const logOut = (): void => {
        appState.setAuthInfo(null, '', '');
    }

    //#endregion

    return (
        <header className="flex p-2 bg-blue-700 dark:bg-gray-800 justify-between">
            <div className="flex">
                <ul className="flex flex-wrap flex-row list-none">
                    <li className="flex-auto">
                        <Link className="block px-2 text-white " to="/" title="Home">
                            <span className="text-2xl animal"></span>
                        </Link>
                    </li>
                    {
                        appState.jwt === null
                            ?
                            <></>
                            :
                            <>
                                <li className="flex-auto">
                                    <NavLink className="block p-2 text-white dark:hover:bg-pink-100 dark:hover:text-black font-semibold" 
                                    to="/todos">
                                        Todo list
                                    </NavLink>
                                </li>
                                <li className="flex-auto">
                                    <NavLink className="block p-2 text-white dark:hover:bg-pink-100 dark:hover:text-black font-semibold" 
                                    to="/categories">
                                        Categories
                                    </NavLink>
                                </li>
                                <li className="flex-auto">
                                    <NavLink className="block p-2 text-white dark:hover:bg-pink-100 dark:hover:text-black font-semibold" 
                                    to="/priorities">
                                        Priorities
                                    </NavLink>
                                </li>
                            </>
                    }
                </ul>
            </div>
            <div className="flex">
                <ul className="flex flex-wrap flex-row list-none">
                    {appState.jwt === null
                        ?
                        <>
                            <li className="flex-auto">
                                <NavLink className="block p-2 text-white hover:bg-pink-100 hover:text-black font-semibold" to="/identity/login">
                                    Login
                                </NavLink>
                            </li>
                            <li className="flex-auto">
                                <NavLink className="block p-2 text-white hover:bg-pink-100 hover:text-black font-semibold" to="/identity/register">
                                    Register
                                </NavLink>
                            </li>
                        </>
                        :
                        <li>
                            <button
                                onClick={() => logOut()}
                                className="block p-2 text-white hover:bg-pink-100 hover:text-black font-semibold">
                                Log Out
                            </button>
                        </li>
                    }
                </ul>
                {
                    isDarkTheme()
                        ?
                        <button className="text-white" onClick={() => changeTheme('')}>
                            {buttonValue}
                        </button>
                        :
                        <button onClick={() => changeTheme('dark')}>
                            {buttonValue}
                        </button>
                }
            </div>
        </header >
    )
}

export default Header;
