import Axios, { AxiosError } from "axios";
import { LoginUrl } from "../configuration";
import { IFetchResponse } from "../types/IFetchResponse";
import { ILoginValues } from '../dtos/ILoginValues';
import { IRegisterValues } from "../dtos/IRegisterValues";

export abstract class AccountService {

    protected static axios = Axios.create({
        baseURL: LoginUrl,
        headers: {
            'Content-Type': 'application/json',
        }
    });

    static login = async<TEntity>(loginData: ILoginValues): Promise<IFetchResponse<TEntity[]>> => {
        try {
            let response = await AccountService.axios.post('Login', JSON.stringify(loginData));

            return {
                ok: response.status <= 299,
                statusCode: response.status,
                data: response.data
            }
        } catch (error) {
            let axError = error as AxiosError;
            return {
                ok: false,
                statusCode: axError.response?.status ?? 500,
                data: axError.response?.data,
                messages: axError.response?.data.messages
            }
        }
    }


    static register = async <TEntity>(registerData: IRegisterValues): Promise<IFetchResponse<TEntity[]>> => {
        try {
            let response = await AccountService.axios.post('Register', JSON.stringify(registerData));
            return {
                ok: response.status <= 299,
                statusCode: response.status,
                data: response.data
            }
        } catch (error) {
            let axError = error as AxiosError;
            return {
                ok: false,
                statusCode: axError.response?.status ?? 500,
                data: axError.response?.data,
                messages: axError.response?.data.messages
            }
        }
    }

}
