import { ITodoCategory } from "../../dtos/ITodoCategory";

export interface ICategoryCardProps {
    categories: ITodoCategory[];
}

const CategoryCards = (props: ICategoryCardProps) => {

    const CategoryCard = (category: ITodoCategory) => {
        return (
            <div className="p-4 border-2 border-white rounded-md bg-white text-blue-500">
                <div className="font-semibold">
                    {category.categoryName}
                </div>
                <div>
                    {category.categorySort}
                </div>
            </div>
        )
    }

    return (
        <div className="flex justify-evenly border-b border-t border-white p-4">
            {
                props.categories.map(category => {
                    return <CategoryCard {...category} key={category.id} />;
                })
            }
        </div>)



}


export default CategoryCards;
