import { ITodoTask } from "../../dtos/ITodoTask";
import { initialSelectedTodo } from "./TodosIndex";

export interface ITodosListProps {
    values: ITodoTask[];
    setSelectedTodo: (todo: ITodoTask) => void;
    selectedTodo: ITodoTask;
    setTodoCompletedValue: (todo: ITodoTask) => void;
}

const TodoList = (props: ITodosListProps) => {
    const toDateString = (date: Date, separator: string = "."): string => {

        return `${date.getDate() > 9 ? date.getDate() : "0" + date.getDate()}${separator}${(date.getMonth() + 1) > 9 ? (date.getMonth() + 1) : "0" + (date.getMonth() + 1)}${separator}${date.getFullYear()}`
    }
    return (
        <div className="flex justify-center p-2 border-t border-b border-1">
            <div className="flex flex-col sm:w-120 w-96 cursor-default">

                <div className="flex flex-row justify-between font-semibold text-white">
                    <div className="sm:w-2/4 w-2/3">Task</div>
                    <div className="sm:w-1/4 sm:block hidden">Added</div>
                    <div className="sm:w-1/4 w-1/3">Due date</div>
                    <div className="w-4"></div>
                </div>
                <div className="space-y-2">
                    {props.values
                        .sort((a, b) => {
                            if (a.isCompleted === b.isCompleted) {
                                return (
                                    new Date(a.dueDt ?? "") < new Date(b.dueDt ?? "") ? -1 : 1
                                )
                            } else if (a.isCompleted) {
                                return 1
                            } else {
                                return -1
                            }
                        })
                        .map(todo => {
                            if (todo.id === props.selectedTodo.id) {
                                return (
                                    <div className="flex flex-row justify-between p-1 border rounded-md border-yellow-100 bg-yellow-200 text-black">
                                        <div
                                            onClick={(e) => props.selectedTodo.id === todo.id ? props.setSelectedTodo(initialSelectedTodo) : props.setSelectedTodo(todo)}
                                            className="flex flex-row justify-between w-full" id={todo.id}>
                                            <div className="sm:w-2/4  w-2/3">{todo.taskName}</div>
                                            <div className="sm:w-1/4 sm:block hidden">{toDateString(new Date(todo.createdDt ?? new Date()))}</div>
                                            <div className="sm:w-1/4  w-1/3">{toDateString(new Date(todo.dueDt))}</div>
                                        </div>
                                        <div><input className="h-4 w-4" type="checkbox" checked={todo.isCompleted} onChange={() => { props.setTodoCompletedValue(todo) }}></input></div>
                                    </div>
                                )
                            } else {
                                if (!todo.isCompleted) {
                                    return (
                                        <div className="flex flex-row justify-between p-1 border rounded-md border-white hover:bg-yellow-200 hover:text-black text-white">
                                            <div
                                                onClick={(e) => props.selectedTodo.id === todo.id ? props.setSelectedTodo(initialSelectedTodo) : props.setSelectedTodo(todo)}
                                                className="flex flex-row justify-between w-full" id={todo.id}>
                                                <div className="sm:w-2/4  w-2/3">{todo.taskName}</div>
                                                <div className="sm:w-1/4 sm:block hidden">{toDateString(new Date(todo.createdDt ?? new Date()))}</div>
                                                <div className="sm:w-1/4  w-1/3">{toDateString(new Date(todo.dueDt))}</div>
                                            </div>
                                            <div>
                                                <input className="h-4 w-4" type="checkbox" checked={todo.isCompleted} onChange={() => { props.setTodoCompletedValue(todo) }}></input>
                                            </div>
                                        </div>
                                    )
                                } else {
                                    return (
                                        <div className="flex flex-row justify-between p-1 border rounded-md border-white hover:bg-yellow-200 hover:text-black text-white opacity-30">
                                            <div
                                                onClick={(e) => props.selectedTodo.id === todo.id ? props.setSelectedTodo(initialSelectedTodo) : props.setSelectedTodo(todo)}
                                                className="flex flex-row justify-between w-full" id={todo.id}>
                                                <div className="sm:w-2/4  w-2/3">{todo.taskName}</div>
                                                <div className="sm:w-1/4 sm:block hidden">{toDateString(new Date(todo.createdDt ?? new Date()))}</div>
                                                <div className="sm:w-1/4  w-1/3">{toDateString(new Date(todo.dueDt))}</div>
                                            </div>
                                            <div>
                                                <input className="h-4 w-4" type="checkbox" checked={todo.isCompleted} onChange={() => { props.setTodoCompletedValue(todo) }}></input>
                                            </div>
                                        </div>
                                    )
                                }
                            }
                        })}
                </div>
            </div>
        </div>
    )
}

export default TodoList;