import { ITodoCategory } from "../../dtos/ITodoCategory";
import { ITodoPriority } from "../../dtos/ITodoPriority";
import { ITodoTask } from "../../dtos/ITodoTask";
import { IPageStatus } from "../../types/IPageStatus";
import DatePicker from "react-datepicker";
import { Link } from "react-router-dom";


export interface ITodoAddProps {
    values: ITodoTask;
    handleChange: (target: HTMLInputElement | HTMLSelectElement) => void;
    handleDateChange: (target: Date) => void;
    addTodo: (target: EventTarget) => void;
    pageStatus: IPageStatus;
    categories: ITodoCategory[];
    priorities: ITodoPriority[];
    date: Date;
    generateDefaultPriorities: () => void;
    generateDefaultCategories: () => void;
}

const TodosAdd = (props: ITodoAddProps) => {

    return (
        <div className="flex justify-center p-2 border-t border-b border-1">
            <form className="p-2 w-96 sm:w-120 text-black">
                <div className="flex w-full flex-col space-y-4">
                    <div className="flex flex-col space-y-4">
                        <div className="flex flex-col">
                            <label className="text-white font-semibold">What do you need to do?</label>
                            <input id="taskName"
                                type="text"
                                className="p-1"
                                value={props.values.taskName}
                                onChange={(e) => props.handleChange(e.target)}
                            >
                            </input>
                        </div>
                        <div className="flex flex-col">
                            <label className="text-white font-semibold">When does it have to be done?</label>
                            <DatePicker
                                className="w-full"
                                selected={new Date(props.date)}
                                onChange={(date) => props.handleDateChange(date as Date)}
                                dateFormat="dd.MM.yyyy"
                                minDate={new Date(Date.now())}
                            />
                        </div>
                    </div>
                    <div className="flex flex-row justify-between">
                        <div className="flex flex-col w-1/3">
                            <label htmlFor="todoCategoryId" className="text-white font-semibold">Categorize!</label>
                            {
                                props.categories.length > 0
                                    ?
                                    <select id="todoCategoryId"
                                        value={props.values.todoCategoryId}
                                        onChange={(e) => props.handleChange(e.target)}
                                        className="p-1"
                                    >
                                        <option value=""></option>
                                        {
                                            props.categories.map(category => {
                                                return (
                                                    <option
                                                        value={category.id}
                                                        key={category.id}
                                                    >
                                                        {category.categoryName}
                                                    </option>
                                                )
                                            })
                                        }
                                    </select>
                                    :
                                    <>
                                        <Link
                                            className="hover:text-pink-100 font-semibold underline text-white"
                                            to="/categories/add">
                                            Add categories
                                        </Link>
                                        <button
                                            onClick={(e) => { props.generateDefaultCategories(); e.preventDefault() }}
                                            className="hover:text-pink-100 font-semibold underline text-white text-left">
                                            Generate default
                                        </button>
                                    </>
                            }
                        </div>
                        <div className="flex flex-col w-1/3">
                            <label className="text-white font-semibold">Prioritize!</label>
                            {
                                props.priorities.length > 0
                                    ?
                                    <select id="todoPriorityId"
                                        value={props.values.todoPriorityId}
                                        onChange={(e) => props.handleChange(e.target)}
                                        className="p-1"
                                    >
                                        <option value=""></option>
                                        {
                                            props.priorities.map(priority => {
                                                return (
                                                    <option
                                                        value={priority.id}
                                                        key={priority.id}
                                                    >
                                                        {priority.priorityName}
                                                    </option>
                                                )
                                            })
                                        }
                                    </select>
                                    :
                                    <>
                                        <Link
                                            className="hover:text-pink-100 font-semibold underline text-white"
                                            to="/priorities/add">
                                            Add priorities
                                        </Link>
                                        <button
                                            onClick={(e) => { props.generateDefaultPriorities(); e.preventDefault() }}
                                            className="hover:text-pink-100 font-semibold underline text-white text-left">
                                            Generate default
                                        </button>
                                    </>

                            }
                        </div>
                    </div>
                    <div className="flex flex-row justify-center">
                        <button
                            className="p-2 bg-green-600 w-full font-bold text-white"
                            onClick={(e) => { props.addTodo(e.target); e.preventDefault() }}>
                            Add
                        </button>
                    </div>
                </div>
            </form>
        </div>
    )
}

export default TodosAdd;