import React from "react";
import { useContext, useEffect, useState } from "react";
import { Redirect } from "react-router";
import Loader from "../../components/Loader";
import { AppContext } from "../../context/AppContext";
import { ITodoCategory } from "../../dtos/ITodoCategory";
import { ITodoPriority } from "../../dtos/ITodoPriority";
import { ITodoTask } from "../../dtos/ITodoTask";
import { BaseService } from "../../services/base-service";
import { EPageStatus } from "../../types/EPageStatus";
import { IPageStatus } from "../../types/IPageStatus";
import TodoList from "./TodoList";
import TodosAdd from "./TodosAdd";
import TodosEdit from "./TodosEdit";

const initialFormValues: ITodoTask = {
    taskName: "",
    taskSort: 0,
    dueDt: new Date(Date.now()),
    isCompleted: false,
    isArchived: false,
    todoCategoryId: "",
    todoPriorityId: "",
}

export const initialSelectedTodo: ITodoTask = {
    id: "",
    taskName: "",
    taskSort: 0,
    dueDt: new Date(Date.now()),
    isCompleted: false,
    isArchived: false,
    todoCategoryId: "",
    todoPriorityId: "",
}

const TodosIndex = () => {
    const [formValues, setFormValues] = useState(initialFormValues);
    const [todos, setTodos] = useState([] as ITodoTask[]);
    const [pageStatus, setPageStatus] = useState({ pageStatus: EPageStatus.Loading, statusCode: -1 } as IPageStatus);
    const [categories, setCategories] = useState([] as ITodoCategory[]);
    const [priorities, setPriorities] = useState([] as ITodoPriority[]);
    const [dueDate, setDueDate] = useState(new Date(Date.now()));
    const [selectedTodo, setSelectedTodo] = useState(initialSelectedTodo)

    const appState = useContext(AppContext);

    const loadData = async () => {
        let result = await BaseService.getAll<ITodoTask>('/TodoTasks', appState.jwt ?? "");

        if (result.ok && result.data) {
            setTodos(result.data);

            let categoryResult = await BaseService.getAll<ITodoCategory>('/TodoCategories', appState.jwt ?? "");

            if (categoryResult.ok && categoryResult.data) {
                setCategories(categoryResult.data);
            }

            let priorityResult = await BaseService.getAll<ITodoPriority>('/TodoPriorities', appState.jwt ?? "");
            if (priorityResult.ok && priorityResult.data) {
                setPriorities(priorityResult.data);
            }
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: result.statusCode });

        } else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: result.statusCode, messages: result.messages });
        }
    }

    const handleChange = (target: HTMLInputElement | HTMLSelectElement) => {
        switch (target.id) {
            case "taskName":
                setFormValues({ ...formValues, taskName: target.value })
                return;
            case "todoCategoryId":
                setFormValues({ ...formValues, todoCategoryId: target.value })
                return;
            case "todoPriorityId":
                setFormValues({ ...formValues, todoPriorityId: target.value })
                return;
            default:
                return;
        }

    }

    const handleDateChange = (target: Date) => {
        setDueDate(target)

        setFormValues({
            ...formValues,
            dueDt: target
        })
    }

    const handleEditChange = (target: HTMLInputElement | HTMLSelectElement) => {
        switch (target.id) {
            case "taskName":
                setSelectedTodo({ ...selectedTodo, taskName: target.value })
                return;
            case "todoCategoryId":
                setSelectedTodo({ ...selectedTodo, todoCategoryId: target.value })
                return;
            case "todoPriorityId":
                setSelectedTodo({ ...selectedTodo, todoPriorityId: target.value })
                return;
            default:
                return;
        }

    }

    const handleEditDateChange = (target: Date) => {
        setSelectedTodo({
            ...selectedTodo,
            dueDt: target
        })
    }

    const addTodo = async (e: EventTarget) => {
        setPageStatus({
            pageStatus: EPageStatus.Loading, statusCode: -1
        });

        let result = await BaseService.post<ITodoTask>('/TodoTasks', formValues, appState.jwt ?? "")

        console.log(result);


        if (result.ok) {
            setFormValues(initialFormValues);
            setDueDate(new Date(Date.now()))
            setPageStatus({
                pageStatus: EPageStatus.Ok, statusCode: result.statusCode,
            })
            loadData();
        } else {
            setPageStatus({
                pageStatus: EPageStatus.Error, statusCode: result.statusCode, messages: result.messages,
            })
        }
    }

    const editTodo = async (e: EventTarget) => {
        setPageStatus({
            pageStatus: EPageStatus.Loading, statusCode: -1
        })

        let result = await BaseService.put<ITodoTask>('/TodoTasks', selectedTodo, selectedTodo.id ?? "", appState.jwt ?? "")
        if (result.ok) {
            setPageStatus({
                pageStatus: EPageStatus.Ok, statusCode: result.statusCode
            })
            setSelectedTodo(initialSelectedTodo);
            loadData();
        } else {
            setPageStatus({
                pageStatus: EPageStatus.Error, statusCode: result.statusCode, messages: result.messages
            })
        }
    }

    const setTodoCompletedValue = async (todo: ITodoTask) => {
        todo.isCompleted ? todo.isCompleted = false : todo.isCompleted = true;

        setPageStatus({
            pageStatus: EPageStatus.Loading, statusCode: -1
        })

        let result = await BaseService.put<ITodoTask>('/TodoTasks', todo, todo.id ?? "", appState.jwt ?? "")

        if (result.ok) {
            setPageStatus({
                pageStatus: EPageStatus.Ok, statusCode: result.statusCode
            })
            loadData();
        } else {
            setPageStatus({
                pageStatus: EPageStatus.Error, statusCode: result.statusCode, messages: result.messages
            })
        }

    }

    const generateDefaultPriorities = async () => {

        const lowPriority = { priorityName: "Low", prioritySort: 0 } as ITodoPriority;
        const normalPriority = { priorityName: "Normal", prioritySort: 1 } as ITodoPriority;
        const highPriority = { priorityName: "High", prioritySort: 2 } as ITodoPriority;

        setPageStatus({
            pageStatus: EPageStatus.Loading, statusCode: -1
        })

        let lowResult = await BaseService.post<ITodoPriority>('/TodoPriorities', lowPriority, appState.jwt ?? "");
        if (!lowResult.ok) {
            setPageStatus({
                pageStatus: EPageStatus.Error, statusCode: lowResult.statusCode, messages: lowResult.messages
            })
            return;
        }

        let normResult = await BaseService.post<ITodoPriority>('/TodoPriorities', normalPriority, appState.jwt ?? "");
        if (!normResult.ok) {
            setPageStatus({
                pageStatus: EPageStatus.Error, statusCode: normResult.statusCode, messages: normResult.messages
            })
            return;
        }

        let highResult = await BaseService.post<ITodoPriority>('/TodoPriorities', highPriority, appState.jwt ?? "");
        if (!highResult.ok) {
            setPageStatus({
                pageStatus: EPageStatus.Error, statusCode: highResult.statusCode, messages: highResult.messages
            })
            return;
        }

        setPageStatus({
            pageStatus: EPageStatus.Ok, statusCode: highResult.statusCode
        })

        loadData();
    }

    const generateDefaultCategories = async () => {

        const homeCategory = { categoryName: "Home", categorySort: 0 } as ITodoCategory;
        const workCategory = { categoryName: "Work", categorySort: 1 } as ITodoCategory;

        setPageStatus({
            pageStatus: EPageStatus.Loading, statusCode: -1
        })

        let homeResult = await BaseService.post<ITodoCategory>('/TodoCategories', homeCategory, appState.jwt ?? "");
        if (!homeResult.ok) {
            setPageStatus({
                pageStatus: EPageStatus.Error, statusCode: homeResult.statusCode, messages: homeResult.messages
            })
            return;
        }

        let workResult = await BaseService.post<ITodoCategory>('/TodoCategories', workCategory, appState.jwt ?? "");
        if (!workResult.ok) {
            setPageStatus({
                pageStatus: EPageStatus.Error, statusCode: workResult.statusCode, messages: workResult.messages
            })
            return;
        }

        setPageStatus({
            pageStatus: EPageStatus.Ok, statusCode: workResult.statusCode
        })

        loadData();
    }

    useEffect(() => {
        loadData();
    }, [])

    return (
        <>
            <Loader pageStatus={pageStatus} />
            {appState.jwt !== null
                ?
                <div className="flex lg:flex-row flex-col justify-evenly">
                    <div className="flex flex-col lg:space-y-12">

                        {
                            selectedTodo.id !== ""
                                ?
                                <TodosEdit
                                    values={selectedTodo}
                                    handleChange={handleEditChange}
                                    handleDateChange={handleEditDateChange}
                                    categories={categories}
                                    priorities={priorities}
                                    editTodo={editTodo}
                                />
                                :
                                <TodosAdd
                                    values={formValues}
                                    handleChange={handleChange}
                                    addTodo={addTodo}
                                    pageStatus={pageStatus}
                                    categories={categories}
                                    priorities={priorities}
                                    handleDateChange={handleDateChange}
                                    date={dueDate}
                                    generateDefaultPriorities={generateDefaultPriorities}
                                    generateDefaultCategories={generateDefaultCategories}
                                />
                        }
                    </div>
                    <TodoList
                        values={todos}
                        setSelectedTodo={setSelectedTodo}
                        selectedTodo={selectedTodo}
                        setTodoCompletedValue={setTodoCompletedValue}
                    />
                </div>
                :
                <Redirect to="/" />
            }

        </>
    )
}

export default TodosIndex;
