import { ITodoPriority } from "../../dtos/ITodoPriority";

export interface IPriorityCardProps {
    priorities: ITodoPriority[];
}

const PriorityCards = (props: IPriorityCardProps) => {

    const PriorityCard = (priority: ITodoPriority) => {
        return (
            <div className="p-4 border-2 border-white rounded-md bg-white text-blue-500">
                <div className="font-semibold">
                    {priority.priorityName}
                </div>
                <div>
                    {priority.prioritySort}
                </div>
            </div>
        )
    }

    return (

        <div className="flex justify-evenly border-b border-t border-white p-4">
            {
                props.priorities.map(priority => {
                    return <PriorityCard {...priority} key={priority.id} />;
                })
            }
        </div>
    )
}

export default PriorityCards;