import { useContext } from "react";
import { Link } from "react-router-dom";
import { AppContext } from "../../context/AppContext";

const HomeIndex = () => {
    const appState = useContext(AppContext);
    return (
        <div className="flex flex-col cursor-default">
            <p className="text-9xl font-bold text-pink-200">Todos.</p>
            {
                appState.jwt === null
                    ?
                    <p className="text-2xl font-bold text-pink-200 pl-6">
                        <Link className="hover:text-white" to="/identity/login">Login</Link> to get started
                    </p>
                    :
                    <p className="text-2xl font-bold text-pink-200 pl-6 italic">
                        You miss 100% of the shots you don't take.
                    </p>
            }
        </div>
    )
}

export default HomeIndex;
