import { useContext } from "react";
import { Link } from "react-router-dom"
import { AppContext } from "../context/AppContext";

const Page404 = () => {
    const appState = useContext(AppContext);
    return (
        <div className="flex flex-row justify-center text-white">
            <div className="flex flex-col space-y-20">
                <div>Sadly, this page does not exist.</div>
                <div className="flex flex-row justify-evenly font-semibold">
                    <Link to="/">Home</Link>|
                    {
                        appState.jwt === null
                            ?
                            <>
                                <Link to="/identity/login">Login</Link>|
                                <Link to="/identity/register">Register</Link>
                            </>
                            :
                            <>
                                <Link to="/todos">Todo list</Link>|
                                <Link to="/categories">Categories</Link>|
                                <Link to="/priorities">Priorities</Link>
                            </>
                    }
                </div>
            </div>
        </div>
    )
}

export default Page404;
