import { ApiBaseUrl } from './../configuration';
import Axios, { AxiosError, AxiosRequestConfig } from 'axios';
import { IFetchResponse } from '../types/IFetchResponse';

export abstract class BaseService {
    protected static _axios = Axios.create({
        baseURL: ApiBaseUrl,
        headers: {
            'Content-Type': 'application/json',
        }
    });

    protected static getAxiosConfiguration(jwt?: string): AxiosRequestConfig | undefined {
        if (!jwt) return undefined;
        const config: AxiosRequestConfig = {
            headers: {
                Authorization: 'Bearer ' + jwt
            }
        };

        return config;
    }

    static getAll = async <TEntity>(apiEndpoint: string, jwt?: string): Promise<IFetchResponse<TEntity[]>> => {
        try {
            let response = await BaseService._axios
                .get<TEntity[]>(apiEndpoint, BaseService.getAxiosConfiguration(jwt));
            return {
                ok: response.status <= 299,
                statusCode: response.status,
                data: response.data as TEntity[]
            }
        } catch (error) {
            let axError = error as AxiosError;
            return {
                ok: false,
                statusCode: axError.response?.status ?? 500,
                data: axError.response?.data,
                messages: axError.response?.data.messages
            }
        }
    }

    static get = async <TEntity>(apiEndpoint: string, id: string, jwt: string): Promise<IFetchResponse<TEntity>> => {
        try {
            let response = await BaseService._axios.get<TEntity>(`${apiEndpoint}/${id}`, BaseService.getAxiosConfiguration(jwt));
            return {
                ok: response.status <= 299,
                statusCode: response.status,
                data: response.data as TEntity
            }
        } catch (error) {
            let axError = error as AxiosError;
            return {
                ok: false,
                statusCode: axError.response?.status ?? 500,
                data: axError.response?.data,
                messages: axError.response?.data.messages
            }
        }
    }

    static post = async <TEntity>(apiEndpoint: string, entity: TEntity, jwt: string): Promise<IFetchResponse<TEntity>> => {
        try {
            let response = await BaseService._axios.post<TEntity>(apiEndpoint, JSON.stringify(entity), BaseService.getAxiosConfiguration(jwt));
            return {
                ok: response.status <= 299,
                statusCode: response.status,
                data: response.data as TEntity
            }
        } catch (error) {
            let axError = error as AxiosError;
            return {
                ok: false,
                statusCode: axError.response?.status ?? 500,
                data: axError.response?.data,
                messages: axError.response?.data.messages
            }
        }
    }

    static put = async <TEntity>(apiEndpoint: string, entity: TEntity, id: string, jwt: string): Promise<IFetchResponse<TEntity>> => {
        try {
            let response = await BaseService._axios.put<TEntity>(`${apiEndpoint}/${id}`, JSON.stringify(entity), BaseService.getAxiosConfiguration(jwt));
            return {
                ok: response.status <= 299,
                statusCode: response.status,
                data: response.data
            }
        } catch (error) {
            let axError = error as AxiosError;
            return {
                ok: false,
                statusCode: axError.response?.status ?? 500,
                data: axError.response?.data,
                messages: axError.response?.data.messages
            }
        }
    }

    static delete = async <TEntity>(apiEndpoint: string, id: string, jwt: string): Promise<IFetchResponse<TEntity>> => {
        try {
            let response = await BaseService._axios.delete<TEntity>(`${apiEndpoint}/${id}`, BaseService.getAxiosConfiguration(jwt));
            return {
                ok: response.status <= 299,
                statusCode: response.status,
                data: response.data
            }
        } catch (error) {
            let axError = error as AxiosError;
            return {
                ok: false,
                statusCode: axError.response?.status ?? 500,
                data: axError.response?.data,
                messages: axError.response?.data.messages
            }
        }
    }
}
