import React, { useState } from 'react';
import { AppContextProvider, initialAppState } from './context/AppContext';
import Header from './components/Header';
import { Route, Switch } from 'react-router-dom';
import Page404 from './containers/Page404';
import TodosIndex from './containers/todos/TodosIndex';
import HomeIndex from './containers/home/HomeIndex';
import LoginForm from './containers/identity/LoginForm';
import RegisterForm from './containers/identity/RegisterForm';
import CategoriesIndex from './containers/categories/CategoriesIndex';
import PrioritiesIndex from './containers/priorities/PrioritiesIndex';

function App() {
    const setAuthInfo = (jwt: string | null, firstName: string, lastName: string): void => {
        setAppState({ ...appState, jwt, firstName, lastName });
    }

    const [appState, setAppState] = useState({ ...initialAppState, setAuthInfo })

    return (
        <AppContextProvider value={appState}>
            <div className="dark:bg-gray-800 bg-blue-700 min-h-screen">
                <Header />
                <main className="p-4">
                    <Switch>
                        <Route exact path="/" component={HomeIndex} />

                        <Route exact path="/identity/login" component={LoginForm} />
                        <Route exact path="/identity/register" component={RegisterForm} />

                        <Route exact path="/todos" component={TodosIndex} />

                        <Route exact path="/categories" component={CategoriesIndex} />

                        <Route exact path="/priorities" component={PrioritiesIndex} />

                        <Route component={Page404} />
                    </Switch>
                </main>
            </div>
        </AppContextProvider>
    );
}

export default App;
