import { EPageStatus } from "../types/EPageStatus";
import { IPageStatus } from "../types/IPageStatus";

const Loader = (props: { pageStatus: IPageStatus }) => {

    const hideErrors = (e: EventInit) => {
        document.querySelector('#error')?.classList.add('hidden');
    }

    switch (props.pageStatus.pageStatus) {
        case EPageStatus.Loading:
            return (
                <div className="absolute top-0 right-0 bottom-0 left-0 w-full h-full bg-gray-600 grid opacity-60 justify-center content-center ">
                    <div className="lds-ring"><div className="dark:boder-white"></div><div></div><div></div><div></div></div>
                </div>
            )
        case EPageStatus.Error:
            return (
                <div id="error" onClick={(e) => hideErrors(e)} className="absolute top-0 bg-red-300 p-4 rounded-br-md">
                    Error {props.pageStatus.statusCode}
                    <br></br>
                    <ul>
                        {props.pageStatus.messages?.map(message => {
                            return <li>{message}</li>
                        })
                        }
                    </ul>
                </div>)
        default:
            return <></>
    }
}

export default Loader;
